import React from 'react';
import {Link} from 'react-router-dom';
import PropTypes from 'prop-types';
import SitePath from '../SitePath/SitePath.js';
import '../../css/style-order.css';


class OrderDone extends React.Component {

  constructor(props) {
    super(props);
    this.state = {
      order: JSON.parse(localStorage.getItem('order'))
    };
  }

  render() {
    const order = this.state.order.data.info;

    return (
      <div className="wrapper order-wrapper">
        <SitePath category={18} />
        <section className="order-done">
          <h2 className="order-done__title order-process__title"> , !</h2>
          <div className="order-done__information order-info">
            <div className="order-info__item order-info__item_summ">
              <h3> : </h3>
              <p>{this.props.total} <i className="fa fa-rub" aria-hidden="true"></i></p>
            </div>
            <div className="order-info__item order-info__item_pay-form">
              <h3> : </h3>
              <p>{order.paymentType}</p>
            </div>
            <div className="order-info__item order-info__item_customer-name">
              <h3> : </h3>
              <p>{order.name}</p>
            </div>
            <div className="order-info__item order-info__item_adress">
              <h3> : </h3>
              <p>{order.address}</p>
            </div>
            <div className="order-info__item order-info__item_phone">
              <h3>: </h3>
              <p>{order.phone}</p>
            </div>
          </div>
          <p className="order-done__notice">
                  <span>nobossanymore@gmail.com.</span>
          </p>
          <Link to='/'>
            <button className="order-done__continue"> </button>
          </Link>
        </section>
      </div>
    )
  }
}

export default OrderDone;



