import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { Router, NavLink, Link, Redirect, withRouter, Route } from 'react-router';
import { History } from 'history';

class SearchForm extends React.PureComponent {
  constructor(props) {
    super(props);
    this.state = { value: '' };
  }

  handleSearch(e) {
    this.setState({
      value: e.target.value.toLowerCase()
    });
  } 

  submitSearch = (e) => {
    console.log(this.state.value);
    if (e.key === 'Enter' || e.key === 13) {
      this.props.history.push(`/catalogue/search=${this.state.value}`);
      e.preventDefault();
    }
  }

  render() {
    return (
      <form className="header-main__search" type="submit">
        <input type="text"
          placeholder=""
          value={this.state.value}
          onChange={this.handleSearch.bind(this)}
          onKeyPress={this.submitSearch}
        />
        <i className="fa fa-search" aria-hidden="true"></i>
      </form>
    )
  }
}
export default withRouter(SearchForm);

