import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { Link } from 'react-router-dom';

class SubHeader extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            status: null,
            categories: [],
            discount: 1
        };
    }

    componentDidMount() {
        fetch('https://api-neto.herokuapp.com/bosa-noga/categories',
            {
                method: "GET",
                mode: 'cors',
                headers: { 'Content-Type': 'application/json' }
            })
            .then(res => res.json())
            .then(res => this.setState({
                status: res.status,
                categories: res.data
            }));
    }

    render() {
        return (
            <div className="wrapper">
                <ul className="main-menu__items">
                    {this.state.categories.map(obj =>
                        <li key={obj.id} className="main-menu__item main-menu__item_">
                            <Link to={`/catalogue/categoryId=${obj.id}`}>{obj.title}</Link>
                        </li>
                    )}
                    <li className="main-menu__item main-menu__item_">
                        <Link to={`/catalogue/discounted=${this.state.discount}`}></Link>
                    </li>
                </ul>
            </div>
        )
    }
}

export default SubHeader;



