import React from 'react';
import {Link} from 'react-router-dom';

export function ProfileHidden() {
    return (
      // 
      <div className="hidden-panel__profile">
        <Link to="/"> </Link>
        <Link to="/favorite">
          <i className="fa fa-heart-o" aria-hidden="true"></i>
        </Link>
        <Link to="/"></Link>
      </div>
    )
  }


