export const Favs = (e, product) => {
  e.preventDefault();

  var prod = {
    categoryId: product.categoryId,
    images: product.images,
    title: product.title,
    brand: product.brand,
    price: product.price
  };

  var serProd = JSON.stringify(prod);

  localStorage.setItem(product.id, serProd);
}


