import React, { Component } from 'react';
import PropTypes from 'prop-types';

const emailPattern = /^[A-Z0-9._-]+@[A-Z0-9-]+.+.[A-Z]{2,4}$/i;


class Subscribe extends React.Component {

  constructor(props) {
    super(props);
    this.state = {
      type: '',
      email: ''
    };
  }

  handleEmail(el) {
    this.setState({ email: el.currentTarget.value });
  }

  submitEmail(e) {
    e.preventDefault();
    if (emailPattern.test(this.state.email) && this.state.type) {
      let subForm = document.querySelector('.subscribe__radios');
      subForm.innerHTML = '<div> ! !</div>';
    } else {
      return
    };
  }

  chooseType = (e) => {this.setState({ type: e.target.value });}

  render() {
    return (
      <section className="subscribe">
        <div className="subscribe__wrapper">
          <h2 className="subscribe__title">    </h2>
          <form className="subscribe__radios" onSubmit={this.submitEmail.bind(this)}>
            <label className="subscribe__radio_label">
              <input onClick={this.chooseType}
                className="subscribe__radio"
                type="radio"
                name="subscribe"
                value="women"
              />
              <div className="subscribe__radio_text"></div>
            </label>
            <label className="subscribe__radio_label">
              <input onClick={this.chooseType}
                className="subscribe__radio"
                type="radio"
                name="subscribe"
                value="men"
              />
              <div className="subscribe__radio_text"></div>
            </label>
            <label className="subscribe__radio_label">
              <input onClick={this.chooseType}
                className="subscribe__radio"
                type="radio"
                name="subscribe"
                value="both"
              />
              <div className="subscribe__radio_text"></div>
            </label>
            <input className="subscribe__email" 
              onInput={this.handleEmail.bind(this)}
              value={this.state.email}
              type="email"
              placeholder=" e-mail"
              required
            />
            <input className="subscribe__submit"
              type="submit"
              value=""
            />
          </form>
        </div>
      </section>
    )
  }
}

export default Subscribe;


