import React, {Component} from 'react';
import {Router, NavLink, Link, Redirect, withRouter, Route} from 'react-router';
import {History} from 'history';
import PropTypes from 'prop-types';
import '../css/style-order.css';

const namePattern = /^[--\s]{3,50}$/;
// const addressPattern = /^[--\s\,]\w+/;
const phonePattern = /^\+?(\d{1,3})?[-.]?\(?(?:\d{2,3})\)?[-.]?\d\d\d[-.]?\d\d\d\d$/;

const order = 'https://api-neto.herokuapp.com/bosa-noga/order/';


class Address extends React.PureComponent {

    constructor(props) {
        super(props);
        this.state = {
            status: null,
            customerName: '',
            cnValid: true,
            customerAddress: '',
            caValid: true,
            customerPhone: '',
            isValid: true,
            paymentType: ''
        };
    }

    confirmOrder = (e) => {
        e.preventDefault();

        if (namePattern.test(this.state.customerName)
            && this.state.customerAddress
            && phonePattern.test(this.state.customerPhone)
            && this.state.paymentType) {
            fetch(order, {
                method: 'POST',
                mode: 'cors',
                headers: { 'Content-Type': 'application/json' },
                body: JSON.stringify({
                    name: this.state.customerName,
                    phone: this.state.customerPhone,
                    address: this.state.customerAddress,
                    paymentType: this.state.paymentType,
                    cart: localStorage.getItem('cartId')
                })
            })
                .then(res => res.json())
                .then(res => {
                    localStorage.setItem('order', JSON.stringify(res));
                    localStorage.removeItem("cartId");
                    this.props.upOrder();
                    this.props.history.push('/order_done/');
                }) //  
                .catch(function (error) { console.log(error); });
            } else {
            console.log(phonePattern.test(this.state.customerPhone));
                return
            };        
    }

    setPT = (e) => {
        this.setState({paymentType: e.target.value});
    }

    handleInput(e) {
        this.setState({[e.target.name]: e.target.value});
    }

    render() {
        return (
            <div className="order-process__confirmed">
                <form onSubmit={this.confirmOrder.bind(this)}>
                    <div className="order-process__delivery">
                        <h3 className="h3" >     ? </h3>
                        <div className="order-process__delivery-form">
                            <label className="order-process__delivery-label">
                                <div className="order-process__delivery-text">  </div>
                                <input onInput={this.handleInput.bind(this)}
                                    value={this.state.customerName}
                                    className="order-process__delivery-input"
                                    type="text"
                                    name="customerName"
                                    placeholder=", "
                                    required
                                />
                            </label>
                            <label className="order-process__delivery-label">
                                <div className="order-process__delivery-text">  </div>
                                <input onInput={this.handleInput.bind(this)}
                                    value={this.state.customerPhone}
                                    className="order-process__delivery-input"
                                    type="tel"
                                    name="customerPhone"
                                    placeholder="   "
                                    required
                                />
                            </label>
                            <label className="order-process__delivery-label">
                                <div className="order-process__delivery-text">  </div>
                                <input onInput={this.handleInput.bind(this)}
                                    value={this.state.customerAddress}
                                    className="order-process__delivery-input order-process__delivery-input_adress"
                                    type='text'
                                    name='customerAddress'
                                    placeholder='      '
                                    required
                                />
                            </label>
                        </div>
                        <p>     .        . </p>
                    </div>
                    <div className="order-process__paid">
                        <h3 className="h3">        ? </h3>
                        <div className="order-process__paid-form">
                            <label className="order-process__paid-label">
                                <input onClick={this.setPT}
                                    className='order-process__paid-radio'
                                    type='radio'
                                    name='paymentType'
                                    value='onlineCard'
                                />
                                <span className="order-process__paid-text">   </span>
                            </label>
                            <label className="order-process__paid-label">
                                <input onClick={this.setPT}
                                    className='order-process__paid-radio'
                                    type='radio'
                                    name='paymentType'
                                    value='offlineCard'
                                />
                                <span className="order-process__paid-text">   </span>
                            </label>
                            <label className="order-process__paid-label">
                                <input onClick={this.setPT}
                                    className='order-process__paid-radio'
                                    type='radio'
                                    name='paymentType'
                                    value='offlineCash'
                                />
                                <span className="order-process__paid-text">   </span>
                            </label>
                        </div>
                    </div>
                    <button type='submit' className="order-process__form-submit order-process__form-submit_click">   </button>
                </form>
            </div>
        )
    }
}

export default withRouter(Address);


